import { useForm, Controller } from "react-hook-form";
import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../../actions/auth";
import { Navigate, useNavigate } from "react-router-dom";
import { Modal } from "bootstrap";
import { getData, postData } from "../../services/user.service";
import { toast } from "react-toastify";

const ChangePass = (props) => {
  let navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [message, setmessage] = useState("");
  const { newpass } = useSelector((state) => state.newpass);

  const dispatch = useDispatch();

  const [ShowModal, SetShowModal] = useState("");
  const [modal, setModal] = useState(null);
  const parseExceptionModal = useRef();

  const [passwordNew, setPasswordNew] = useState("");
  const [passwordNewVerify, setPasswordNewVerify] = useState("");

  const showModalForm = () => {
    const modal = new Modal(parseExceptionModal.current, {
      keyboard: false,
      backdrop: "static",
    });
    setModal(modal);
    modal.show();
  };
  const hideModal = () => {
    modal.hide();
  };

  const {
    register,
    handleSubmit,
    setValue,
    reset,
    control,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    if (props && Object.keys(props).length > 0) {
      setValue("Password", newpass);
      setValue("MSNV", props.user);
    }
  }, [props, props.user]);

  const validateForm = {
    MSNV: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: " khng c hn 10 k t",
      },
    },
    Password: {
      required: "Khng c  trng",
      maxLength: {
        value: 15,
        message: " khng c hn 10 k t",
      },
    },
    passwordNew: {
      required: "Khng c  trng",
      minLength: {
        value: 6,
        message: " khng c t hn 6 k t",
      },
    },
    passwordNewVerify: {
      required: "Khng c  trng",
      minLength: {
        value: 6,
        message: " khng c t hn 6 k t",
      },
    },
  };
  const onChagepass = () => {
    showModalForm();
    SetShowModal("change");
  };
  const onChagepassOK = async (data) => {
    console.log(data);
    if (data.passwordNew !== data.passwordNewVerify) {
      setmessage(" Mt khu xc nhn khng khp");
    } else {
      console.log("first");
      var create = await postData("changePass", {
        username: data.MSNV,
        currentPassword: data.Password,
        newPassword: data.passwordNew,
        confirmPass: data.passwordNewVerify,
      });

      if (create.isSuccess === 1) {
        toast.success("i mt khu thnh cng \n" + create.note, {
          autoClose: 2000,
          className: "",
          position: "top-center",
          theme: "colored",
        });
        setValue("Password", data.passwordNew);

        window.location.reload();
        hideModal();
      } else {
        toast.success("i mt khu tht bi Li: \n" + create.note, {
          autoClose: 2000,
          className: "",
          position: "top-center",
          theme: "colored",
        });
      }
    }
  };

  const onSubmitReg = async (data) => {
    setLoading(true);
    dispatch(login(data.MSNV, data.Password, 0))
      .then(() => {
        navigate("/");
        window.location.reload();
      })
      .catch(() => {
        setLoading(false);
      });
  };
  return (
    <>
      <form>
        <div className="row d-flex justify-content-center align-items-center h-100">
          <div className="">
            <div className="card rounded-3 text-black">
              <div className="">
                <div className="card-body p-md-5 mx-md-4">
                  <div className="text-center">
                    <h3>NG K NHANH </h3>
                    <h4
                      className="mt-1 mb-5 pb-1"
                      style={{ borderBottom: "1px solid black" }}
                    ></h4>
                  </div>

                  <div className="form-outline mb-4">
                    <label className="form-label" htmlFor="username">
                      Ti khon
                    </label>
                    <input
                      id="MSNV"
                      {...register("MSNV")}
                      className="form-control"
                      placeholder="ti khon"
                      readOnly
                    />
                  </div>

                  <div className="form-outline mb-3">
                    <label className="form-label" htmlFor="password">
                      Mt khu
                    </label>
                    <div className="d-flex">
                      <input
                        id="Password"
                        {...register("Password", validateForm.Password)}
                        className="form-control"
                        readOnly
                      />
                    </div>
                    <span
                      className=""
                      style={{
                        color: "red",
                        fontSize: "10px",
                        marginLeft: "145px",
                      }}
                    >
                      {errors.Password?.message}
                    </span>
                  </div>
                  <div className="text-center mb-5 pb-1">
                    {newpass && (
                      <div className="form-group">
                        <div
                          className="alert alert-danger text-center"
                          role="alert"
                        >
                          <p style={{ fontSize: "12px", margin: "0" }}>
                            TO NHANH TI KHON THNH CNG! ANH CH VUI LNG
                            NGHI NH MT KHU
                          </p>
                          <p style={{ fontSize: "12px", margin: "0" }}>
                            HOC THAY I MT KHU
                          </p>
                        </div>
                      </div>
                    )}
                    <div className="row">
                      <div className="col-md-6">
                        <button
                          className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3"
                          type="submit"
                          disabled={loading}
                          onClick={handleSubmit(onChagepass)}
                        >
                          {loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                          )}
                          <span>i Mt Khu</span>
                        </button>
                      </div>
                      <div className="col-md-6">
                        <button
                          className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3"
                          type="submit"
                          disabled={loading}
                          onClick={handleSubmit(onSubmitReg)}
                        >
                          <span>ng Nhp</span>
                        </button>
                      </div>
                      <div
                        className="modal fade"
                        id="modal-xl"
                        data-backdrop="static"
                        ref={parseExceptionModal}
                        aria-labelledby="parseExceptionModal"
                        backdrop="static"
                      >
                        <div
                          className="modal-dialog modal-dialog-scrollable"
                          style={{ maxWidth: "88%" }}
                        >
                          <div className="modal-content">
                            <div className="modal-header border-0 p-0">
                              <button
                                type="button"
                                className="close ml"
                                data-dismiss="modal"
                                onClick={() => hideModal()}
                                aria-label="Close"
                              >
                                <span
                                  aria-hidden="true"
                                  style={{ fontSize: "30px" }}
                                >
                                  
                                </span>
                              </button>
                            </div>
                            <div className="modal-body pt-0">
                              <>
                                {ShowModal === "change" && (
                                  <div className="col-md-6 offset-md-3">
                                    <span
                                      className="anchor"
                                      id="formChangePassword"
                                    />
                                    <hr className="mb-5" />
                                    {/* form card change password */}
                                    <div className="card card-outline-secondary">
                                      <div className="card-header">
                                        <h3 className="mb-0">
                                          THAY I MT KHU
                                        </h3>
                                      </div>
                                      <div className="card-body">
                                        <form
                                          className="form"
                                          role="form"
                                          autoComplete="off"
                                        >
                                          <div className="form-group">
                                            <label>MT KHU C</label>
                                            <input
                                              id="Password"
                                              {...register(
                                                "Password",
                                                validateForm.Password
                                              )}
                                              className="form-control"
                                              readOnly
                                            />
                                          </div>

                                          <div className="form-group">
                                            <label>MT KHU MI</label>
                                            <input
                                              className="form-control"
                                              id="passwordNew"
                                              {...register(
                                                "passwordNew",
                                                validateForm.passwordNew
                                              )}
                                            />
                                          </div>
                                          <p
                                            style={{
                                              fontSize: "10px",
                                              color: "red",
                                            }}
                                          >
                                            {errors.passwordNew?.message}
                                          </p>
                                          <div className="form-group">
                                            <label>NHP LI MT KHU</label>
                                            <input
                                              className="form-control"
                                              id="passwordNewVerify"
                                              {...register(
                                                "passwordNewVerify",
                                                validateForm.passwordNewVerify
                                              )}
                                            />
                                          </div>
                                          <p
                                            style={{
                                              fontSize: "10px",
                                              color: "red",
                                            }}
                                          >
                                            {errors.passwordNewVerify?.message}
                                          </p>
                                          {message && (
                                            <div className="form-group">
                                              <div
                                                className="alert alert-danger text-center"
                                                role="alert"
                                              >
                                                {message}
                                              </div>
                                            </div>
                                          )}
                                          <div className="form-group">
                                            <button
                                              className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3"
                                              type="submit"
                                              disabled={loading}
                                              onClick={handleSubmit(
                                                onChagepassOK
                                              )}
                                            >
                                              {loading && (
                                                <span className="spinner-border spinner-border-sm"></span>
                                              )}
                                              <span> Thay i</span>
                                            </button>
                                          </div>
                                        </form>
                                      </div>
                                    </div>
                                    {/* /form card change password */}
                                  </div>
                                )}
                              </>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </>
  );
};
export default ChangePass;
