import React, { useState, useEffect, useCallback } from "react";
import { useForm, Controller } from "react-hook-form";
import Cookies from "js-cookie";
import { getData, postData } from "../../services/user.service";
import moment from "moment";
import DatePicker from "react-datepicker";
import { Navigate, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import Loading from "../common/loading/Loading";
import { toast } from "react-toastify";

let emID = Cookies.get("empid");

const Register = () => {
  const {
    register,
    handleSubmit,
    reset,
    control,
    formState: { errors },
  } = useForm();
  const [IsLoading, setIsLoading] = useState(false);
  let navigate = useNavigate();
  const [name, setname] = useState("");
  const [departmentName, setDepartmentName] = useState("");
  const [jobpositionName, setJobpositionName] = useState("");
  const [annualLeave, setAnnualLeave] = useState("");
  const [comeDate, setcomeDate] = useState("");
  const [jPLevelName, setJPLevelName] = useState("");
  const [listTypeOff, setlistTypeOff] = useState([]);

  const [fromDate, setFromDate] = useState("");
  const [toDate, setToDate] = useState("");

  useEffect(() => {
    (async () => {
      let data = await getData("getEmpInfo", emID);
      setname(data.rData.FirstName + " " + data.rData.LastName);
      setDepartmentName(data.rData.DepartmentName);
      setJobpositionName(data.rData.JobpositionName);
      setAnnualLeave(data.rData.AnnualLeave);
      setcomeDate(moment(data.rData.ComeDate).format("DD-MM-YYYY"));
      setJPLevelName(data.rData.JPLevelName);

      let dataTypeOff = await getData("dayOffType");
      console.log(dataTypeOff.rData);
      setlistTypeOff(dataTypeOff.rData);
    })();
  }, []);

  const validateForm = {
    MSNV: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: " khng c hn 10 k t",
      },
    },
    reason: {
      required: " Khng c  trng",
    },
    songaynghi: {
      required: " Khng c  trng",
      pattern: {
        value: /^[0-9]*$/,
        message: "Ch c nhp  s",
      },
    },
    NgayBatDau: {
      required: " Khng c  trng",
    },
    offType: {
      required: " Khng c  trng",
    },
  };

  const onSubmitSave = async (data) => {
    setIsLoading(true);
    var create = await postData("day-off-letter", {
      type: data.offType,
      reason: data.reason,
      startdate: moment(new Date(data.NgayBatDau).toISOString()).format(
        "YYYY-MM-DD"
      ),
      period: data.songaynghi,
      address: data.address,
      command: 0,
    });
    console.log(create);
    if (create.isSuccess === 1) {
      toast.success("lu n thnh cng \n" + create.note, {
        autoClose: 2000,
        className: "",
        position: "top-center",
        theme: "colored",
      });

      navigate("/indexListRegister");
      reset();
      setIsLoading(false);
    } else {
      toast.success("lu  tht bi Li \n" + create.note, {
        autoClose: 2000,
        className: "",
        position: "top-center",
        theme: "colored",
      });

      setIsLoading(false);
    }
  };
  const onSubmitSend = async (data) => {
    setIsLoading(true);
    var create = await postData("day-off-letter", {
      type: data.offType,
      reason: data.reason,
      startdate: moment(new Date(data.NgayBatDau).toISOString()).format(
        "YYYY-MM-DD"
      ),
      period: data.songaynghi,
      address: data.address,
      command: 1,
    });
    console.log(create);
    if (create.isSuccess === 1) {
      toast.success("Gi n thnh cng \n" + create.note, {
        autoClose: 2000,
        className: "",
        position: "top-center",
        theme: "colored",
      });

      navigate("/indexListRegister");
      reset();
      setIsLoading(false);
    } else {
      toast.danger("Gi n tht bi Li: \n" + create.note, {
        autoClose: 2000,
        className: "",
        position: "top-center",
        theme: "colored",
      });
      setIsLoading(false);
    }
  };
  return (
    <form>
      <div className="content-wrapper pb-0 ">
        <section className="content" style={{ minHeight: "100%" }}>
          {/* Default box */}
          <div className="card  " style={{ minHeight: "100vh" }}>
            {IsLoading ? (
              <Loading />
            ) : (
              <div className="card-header py-0 border-0">
                <h2 className="p-4" style={{ textAlign: "center" }}>
                  NG K PHP
                </h2>
                <div className="row">
                  <div className="col-md-6 d-flex">
                    MSNV
                    <p style={{ color: "red", minWidth: "74px" }}>(*)</p>
                    <input
                      {...register("MSNV")}
                      value={emID}
                      readOnly
                      className="form-control ml-3 "
                      style={{ maxWidth: "200px" }}
                    />
                  </div>
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}> .V/B.Phn</span>
                    <input
                      className="form-control ml-3"
                      readOnly
                      value={departmentName}
                    />
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}>H v tn</span>
                    <input
                      value={name}
                      readOnly
                      className="form-control ml-3 "
                    />
                  </div>
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}> Chc V</span>
                    <input
                      className="form-control ml-3"
                      readOnly
                      value={jPLevelName}
                    />
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}>Ngy vo Lm</span>
                    <input
                      value={comeDate}
                      readOnly
                      className="form-control ml-3 "
                    />
                  </div>
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}> V tr CV</span>
                    <input
                      className="form-control ml-3"
                      value={jobpositionName}
                      readOnly
                    />
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-md-6 d-flex">
                    <span style={{ minWidth: "120px" }}>
                      Loi Php <span style={{ color: "red" }}>*</span>
                    </span>

                    <div className="ml-3 w-100">
                      <select
                        className="form-control"
                        {...register("offType", validateForm.offType)}
                      >
                        <option value="">Chn Loi Php</option>
                        {listTypeOff &&
                          listTypeOff.map((val) => {
                            return (
                              <option value={val.OffTypeID} key={val.OffTypeID}>
                                {val.Name}( {val.Note})
                              </option>
                            );
                          })}
                      </select>
                      <span
                        className=""
                        style={{
                          color: "red",
                          fontSize: "10px",
                        }}
                      >
                        {errors.offType?.message}
                      </span>
                    </div>
                  </div>
                  <div className="col-md-6 d-flex justify-content-between">
                    <span style={{ minWidth: "120px" }}>
                      S php nm hin c
                    </span>
                    <input
                      value={annualLeave}
                      className="form-control ml-3 "
                      style={{ maxWidth: "80px" }}
                      readOnly
                    />
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-md-6 d-flex pr-4 ">
                    <span style={{ minWidth: "120px" }}>
                      Bt u ngh t <span style={{ color: "red" }}>*</span>
                    </span>
                    <div className="input-group ml-3 ">
                      <Controller
                        control={control}
                        name="NgayBatDau"
                        render={({ field }) => (
                          <DatePicker
                            className="form-control "
                            dateFormat="dd/MM/yyyy"
                            placeholderText="Chn ngy bt u"
                            onChange={(date) => field.onChange(date)}
                            selected={field.value}
                          />
                        )}
                        rules={validateForm.NgayBatDau}
                      />
                      {errors.NgayBatDau && (
                        <span
                          className=""
                          style={{
                            color: "red",
                            fontSize: "10px",
                          }}
                        >
                          {errors.NgayBatDau.message}
                        </span>
                      )}
                    </div>
                  </div>
                  <div className="col-md-6 d-flex justify-content-between">
                    <span style={{ minWidth: "120px" }}>
                      S Ngy Ngh <span style={{ color: "red" }}>*</span>
                    </span>

                    <div className="w-100 ml-3" style={{ maxWidth: "120px" }}>
                      <input
                        type="text"
                        className="form-control "
                        {...register("songaynghi", validateForm.songaynghi)}
                      />
                      <span
                        className=""
                        style={{
                          color: "red",
                          fontSize: "10px",
                        }}
                      >
                        {errors.songaynghi?.message}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="row mt-2">
                  <div className="col-md-12 d-flex">
                    <span style={{ minWidth: "120px" }}>
                      L do ngh php<span style={{ color: "red" }}>*</span>
                    </span>
                    <textarea
                      {...register("reason", validateForm.reason)}
                      type="text"
                      className="form-control ml-3 "
                    />
                  </div>
                  <span
                    className=""
                    style={{
                      color: "red",
                      fontSize: "10px",
                      marginLeft: "145px",
                    }}
                  >
                    {errors.reason?.message}
                  </span>
                </div>
                <div className="row mt-2">
                  <div className="col-md-12 d-flex">
                    <span style={{ minWidth: "120px" }}>a ch ngh php</span>
                    <textarea
                      type="text"
                      className="form-control ml-3 "
                      {...register("address")}
                    />
                  </div>
                </div>
                <div className="mt-3 d-flex justify-content-between">
                  <Link
                    to="/indexListRegister"
                    className="btn btn  btn-success "
                  >
                    <i className="fas fa-arrow-left"></i> Quay Li
                  </Link>

                  <div>
                    <button
                      className="btn btn  btn-success border border-light mr-3 "
                      type="submit"
                      onClick={handleSubmit(onSubmitSave)}
                    >
                      Lu
                    </button>
                    <button
                      className="btn btn  btn-success border border-light mr-5"
                      type="submit"
                      onClick={handleSubmit(onSubmitSend)}
                    >
                      Gi
                    </button>
                  </div>
                </div>
                {/* end */}
              </div>
            )}
          </div>
        </section>
      </div>
    </form>
  );
};
export default Register;
